
.LoadingTodo-conteiner{
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    width:80vw;
}

.LoadingTodo-text{
    margin:24px 0 24px 24px;
    
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}
.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon{
    border-radius: 50px;
    height:48px;
    width: 48px;
}
.LoadingTodo-completeIcon{
    /* position:absolute;
    left:12px; */
}

.LoadingTodo-deleteIcon{
    align-self: flex-start;
    /* position:absolute;
    top:-24px;
    right:0; */
}
.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon,
.LoadingTodo-conteiner{
    background: linear-gradient(90deg, rgba(250, 250, 250, 1), rgb(200,199,199));
    background-size: 400% 400%;
    animation: loadingAnimation 5s ease-in-out infinite;
}

@keyframes loadingAnimation{
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}
