.TodoItem{
    background-color: white;
    margin:10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    width:80vw;
}
.TodoItem-p {
    color: #1E1E1F;
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }

